import Image from "next/image"
import { User, Twitter, Instagram, Youtube, Linkedin } from "lucide-react"

export  function instancia() {
  return (
    <div className="min-h-screen bg-[#ffffff]">
      {/* Header */}
      <header className="border-b border-[#e3e3e3] bg-[#ffffff]">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <div className="flex items-center">
              <div className="w-8 h-8 bg-[#000000] rounded-sm flex items-center justify-center">
                <div className="grid grid-cols-2 gap-0.5 w-4 h-4">
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                </div>
              </div>
            </div>

            {/* Navigation */}
            <nav className="flex items-center space-x-6">
              <a href="#" className="text-[#767676] hover:text-[#000000] text-sm font-medium">
                Solutions
              </a>
              <a href="#" className="text-[#767676] hover:text-[#000000] text-sm font-medium">
                Pricing
              </a>
              <a href="#" className="text-[#767676] hover:text-[#000000] text-sm font-medium">
                Contact
              </a>
              <a href="#" className="text-[#767676] hover:text-[#000000] text-sm font-medium">
                Link
              </a>
              <button className="px-3 py-1.5 text-sm font-medium text-[#767676] border border-[#d9d9d9] rounded-md hover:bg-[#f5f5f5]">
                Sign in
              </button>
              <button className="px-3 py-1.5 text-sm font-medium bg-[#000000] text-[#ffffff] rounded-md hover:bg-[#2c2c2c]">
                Register
              </button>
            </nav>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-12">
          {/* Left side - Image */}
          <div>
            <Image
              src="/donald.jpg"
              alt="McDonald's Restaurant"
              width={200}
              height={150}
              className="w-[400px] h-auto rounded-lg"
            />
          </div>

          {/* Right side - Content */}
          <div className="space-y-6">
            <h1 className="text-4xl font-bold text-[#000000]">Totalmente não é um MC</h1>

            <input
              type="text"
              placeholder="Isso aqui não é um mcdonalds... eu juro"
              className="w-full px-3 py-2 border border-[#d9d9d9] rounded-md focus:outline-none focus:ring-2 focus:ring-[#000000] focus:border-transparent"
            />

            <div className="grid grid-cols-2 gap-4">
              <button className="px-4 py-2 bg-[#2c2c2c] text-[#ffffff] rounded-md hover:bg-[#1e1e1e] font-medium">
                Adicionar Item
              </button>
              <button className="px-4 py-2 bg-[#2c2c2c] text-[#ffffff] rounded-md hover:bg-[#1e1e1e] font-medium">
                Remover Item
              </button>
              <button className="px-4 py-2 bg-[#2c2c2c] text-[#ffffff] rounded-md hover:bg-[#1e1e1e] font-medium">
                Config Instancia
              </button>
              <button className="px-4 py-2 bg-[#2c2c2c] text-[#ffffff] rounded-md hover:bg-[#1e1e1e] font-medium">
                Adicionar Funcionário
              </button>
            </div>
          </div>
        </div>

        {/* Two Column Layout */}
        <div
          className="grid grid-cols-1 lg:grid-cols-2 gap-8 p-8 rounded-lg"
          style={{
            backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fillRule='evenodd'%3E%3Cg fill='%23cff7d3' fillOpacity='0.3'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`,
            backgroundColor: "#cff7d3",
          }}
        >
          {/* Left Column - Funcionários */}
          <div>
            <h2 className="text-2xl font-bold text-[#02542d] mb-6">Funcionários</h2>
            <div className="space-y-4">
              {["Cozinheiro1", "Cozinheiro2", "Cozinheiro3", "Cozinheiro4", "Cozinheiro5"].map((cook, index) => (
                <div key={index} className="flex items-center space-x-3 bg-[#ffffff] p-3 rounded-lg">
                  <User className="w-5 h-5 text-[#757575]" />
                  <span className="text-[#000000] font-medium">{cook}</span>
                </div>
              ))}
            </div>
          </div>

          {/* Right Column - Seu estoque */}
          <div>
            <h2 className="text-2xl font-bold text-[#02542d] mb-6">Seu estoque</h2>
            <div className="bg-[#ffffff] p-4 rounded-lg">
              <div className="grid grid-cols-3 gap-2 text-sm font-semibold text-[#000000] mb-3 border-b border-[#e3e3e3] pb-2">
                <span>items</span>
                <span className="text-center">qtd/Kg</span>
                <span className="text-right">Kg</span>
              </div>
              <div className="space-y-1 text-sm">
                {[
                  ["Carne", "10kg"],
                  ["cenoura", "5kg"],
                  ["alface", "3Kg"],
                  ["pão", "8Kg"],
                  ["farinha", "50Kg"],
                  ["cebola", "14Kg"],
                  ["pepino", "6Kg"],
                  ["pimenta", "7Kg"],
                  ["farofa", "100Kg"],
                  ["abacaxi", "10Kg"],
                  ["banana", "50Kg"],
                  ["maça", "10Kg"],
                  ["uva", "10Kg"],
                  ["salame", "40Kg"],
                  ["morango", "60Kg"],
                  ["peixe", "20Kg"],
                  ["tubarão", "40Kg"],
                  ["coxinha", "15Kg"],
                  ["geleia", "10Kg"],
                ].map(([item, quantity], index) => (
                  <div key={index} className="grid grid-cols-3 gap-2 py-1">
                    <span className="text-[#000000]">{item}</span>
                    <span className="text-center text-[#757575]"></span>
                    <span className="text-right text-[#000000]">{quantity}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-[#f5f5f5] border-t border-[#e3e3e3] mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            {/* Logo and Social */}
            <div className="space-y-4">
              <div className="w-8 h-8 bg-[#000000] rounded-sm flex items-center justify-center">
                <div className="grid grid-cols-2 gap-0.5 w-4 h-4">
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                  <div className="w-1.5 h-1.5 bg-[#ffffff] rounded-sm"></div>
                </div>
              </div>
              <div className="flex space-x-3">
                <Twitter className="w-5 h-5 text-[#757575]" />
                <Instagram className="w-5 h-5 text-[#757575]" />
                <Youtube className="w-5 h-5 text-[#757575]" />
                <Linkedin className="w-5 h-5 text-[#757575]" />
              </div>
            </div>

            {/* Use cases */}
            <div>
              <h3 className="font-semibold text-[#000000] mb-4">Use cases</h3>
              <ul className="space-y-2 text-sm text-[#757575]">
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    UI design
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    UX design
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Wireframing
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Diagramming
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Brainstorming
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Online whiteboard
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Team collaboration
                  </a>
                </li>
              </ul>
            </div>

            {/* Explore */}
            <div>
              <h3 className="font-semibold text-[#000000] mb-4">Explore</h3>
              <ul className="space-y-2 text-sm text-[#757575]">
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Design
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Prototyping
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Development features
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Design systems
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Collaboration features
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Design process
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    FigJam
                  </a>
                </li>
              </ul>
            </div>

            {/* Resources */}
            <div>
              <h3 className="font-semibold text-[#000000] mb-4">Resources</h3>
              <ul className="space-y-2 text-sm text-[#757575]">
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Blog
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Best practices
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Colors
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Color wheel
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Support
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Developers
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-[#000000]">
                    Resource library
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </footer>
    </div>
  )
}
